import { useState } from "react";
import ReactPlayer from "react-player/youtube";
import styled from "styled-components";

const VideoPlayer = ({ url, onEnd }) => {
  const [isMuted, setIsMuted] = useState(true);
  const playerConfig = {
    // url: "https://www.youtube.com/watch?v=y2tEPmwWEiI",
    url,
    // controls: true,
    playing: true,
    volume: 1,
    height: "100%",
    width: "100%",
    muted: isMuted,
    playsinline: true,
    onReady: () => {
      setTimeout(() => {
        setIsMuted(false);
      }, 0);
      console.log("Ismuted", isMuted);
    },
    onEnded: onEnd
  };
  return (
    <Box className="player-wrapper">
      <ReactPlayer className="react-player" {...playerConfig} />
      <Button>Next</Button>
    </Box>
  );
};

export default VideoPlayer;

const Box = styled.div`
  .player-wrapper {
    position: relative;
    /* padding-top: 56.25%; */
    /* Player ratio: 100 / (1280 / 720) */
  }

  & > .react-player {
    position: absolute;
    top: 0;
    left: 0;
  }
`;

const Button = styled.button`
  padding: 4px 12px;
  background: #d6f6dd;
  border: none;
`;
